root:has(.mainScreen) {
    width: 100vw;
}

.myButton {
    color: #EEEEEE;
}
.heroIconTiny > .heroModernIcon.heroIconSmall > img {
    max-width: min(50px,13vw);
    max-height: min(50px,13vw);
}

.sliderRow {
    display: flex;
    background-color: #c5734d;
    border-radius: 10px;
    margin-bottom: 1em;
    justify-content: center;
    align-items: center;
}
.sliderRow > img {
    height: 2em;
}
.sliderRow .modernSlider{
    margin-left: 2em;
    margin-right: 2em;
    width: min(10em,80vw);
    height: 3.5em;
}

.modernSlider {
    /*width: 10em;
    max-width: 80vw;*/
}
.modernSlider > span > .MuiSlider-markLabel {
    color: #EEEEEEEE;
}

.counterPackResultModalBox {
    display: flex;
    flex-flow: column;
    padding: 3em 0.2em 1em 0.2em;
    max-width: 400px;
    align-content: center;
    align-items: center;
    position: relative;
}

.myModal {
    display: flex;
    justify-content: center;
    align-content: center;

}

.myModal > * {
    color: #EEEEEE;
}

.closeModal {
    color: #EEEEEE;
    position: absolute !important;
    top: 0.5em;
    right: 0.5em;
}

.closeModal > svg {
    height: 2em;
    width: 2em;
    color: #EEEEEE;
}

.svgButton {
    color: #EEEEEE;
}
.svgButton > svg {
    height: 0.7em;
    width: 0.7em;
    margin-bottom: -0.1em;
    fill: white;
}





.MuiSlider-thumb {
    /*transition: none !important;*/
}

.counterPackResult {
    display: flex;
    flex-direction: column;
}

.filterBar > div {
    color: #eee;
}
.filterBar > div > svg {
    fill: white;
    height: 0.7em;
    width: 0.7em;
}

.counterPackResult > div > span > button > svg {
    height: 0.7em;
    width: 0.7em;
    margin-bottom: -0.1em;
    fill: white;
}


.selectedHeroPack {
    background-color: #321911FF;
    justify-content: center;
    display: flex;
    width: min-content;
    max-width: min-content;
    height: min-content;
    max-height: min-content;
    margin: 0 0 0 0;
    border-radius: 2px;
}

.counterPackResultPacks > div {
    margin: 4px;
}

.selectedHeroPack {
    background-color: #321911FF;
    justify-content: center;
    display: flex;
    width: min-content;
    max-width: min-content;
    height: min-content;
    max-height: min-content;
    margin: 0 0 0 0;
    border-radius: 2px;
    flex-direction: column;
}

.selectedHeroPack.withCurrentStat > .selectedHeroPackExtended {
    filter: opacity(0.5);
}

.selectedHeroPackIcons {
    justify-content: center;
    display: flex;
}
.selectedHeroPackExtended.selectedHeroPackExtendedHydra {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
}

.selectedHeroPackExtended {
    height: 1em;
    line-height: 1em;
    display: grid;
    margin: -2px 2px 2px 2px;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    font-size: 0.8em;
}
.selectedHeroPackExtended > span > img{
    height: 1em;
    line-height: 1em;
}
.filterBarLabel > img {
    height: 1em;
    line-height: 1em;
}

.selectedHeroPackExtendedCurrent {
    height: 1em;
    line-height: 1em;
    display: grid;
    margin: 1px 2px 2px 2px;
    grid-template-columns: 1fr 1fr;
    font-size: 0.9em;
}

.selectedHeroPackExtendedCurrent > span > img{
    height: 1em;
    /*line-height: 2em;*/
}

.selectedHeroPackExtended > span {
    height: 1em;
    line-height: 1em;
}


.counterPackNotFound {
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.headerBlock {
    display: flex;
    flex-direction: row;
    width: 100vw;
    background-color: #8b6b61FF;
    height: 2.1em;
}
.headerBlock > div {
    padding: 0.1em;
    word-break: break-word;
    text-overflow: ellipsis;
    line-height: 1em;
    overflow-y: hidden;
}

.heroModernGrid {
    max-width: 97vw;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.heroModernIcon.mySelected {
    opacity: 30%;
}

.heroModernIcon.myNotHighlighted > span{
    color: #e89379;
}


.heroModernIcon.heroIconSmall > img {
    max-width: 13vw;
    max-height: 13vw;
}

.heroModernIcon {
    position: relative;
    margin: 0;
    justify-items: center;
}
.heroModernIcon > img {
    max-width: 16vw;
    max-height: 16vw;
    min-width: 1em;
    min-height: 1em;
    border-radius: 10px;
    overflow: hidden;
    border: 1px solid white;
    background-color: #5d4037FF;
    pointer-events: none;
}
.heroModernIcon > .heroModernIconName {
    overflow: hidden;
    top: 0;
    left: 0;
    right: 0;
    color: #EEE;
    padding: 0 0 0.1em 0.4em;
    line-height: 1em;
    position: absolute;
    margin: 0.1em 0.2em 0 0.2em;
    max-height: 2.1em;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    font-size: max(0.6em,1vh);
    background-color: #32191177;
    user-select: none;
    pointer-events: none;
    text-align: left;
}
.heroModernIcon > .heroModernIconProb {
    font-size: max(0.6em,1vh);
    line-height: 1em;
    color: #EEE;
    user-select: none;
    padding: 0 0 0.2em 0;
    bottom: 0;
    left: 0;
    right: 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    pointer-events: none;
    position: absolute;
    margin: 0 0.2em 0.2em 0.1em;
    text-align: center;
    background-color: #32191177;
    height: 1.1em;
}

.heroModernIcon > .heroModernIconSpan {
    overflow: hidden;
    pointer-events: none;
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: inherit;
}

.someHeroIcon {
    position: absolute;
    pointer-events: none;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    color: white !important;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-content: center;
    padding-top: 0.5em;
}
.someHeroIcon > svg {
    width: 80%;
    height: 80%;
}



.counterPackScreen {
    width: 100vw;
    height: 100vh;
    margin-top: 1vw;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
}


.mainScreen {
    color: #EEE;
    font-weight: 700;
    width: 100vw;
    background-color: #5d4037;
}

.menuBox {
    display: flex;
    flex-flow: row;
    position:absolute;
    z-index: 1;
    width: 100vw;
    height: 100%;
}

.menuBox > .MuiTabs-root {
    min-width: 5em;
    width: 10em;
    max-width: 90vw;
    background: #8b6b61FF;
    height: 100%;
    -webkit-transition: width 0.6s ease;
    -moz-transition: width 0.6s ease;
    -o-transition: width 0.6s ease;
    transition: width 0.6s ease;
}
.menuFadeBox {
    width: 100%;
    background: rgba(0,0,0,0.3);
}

.searchBox {
    margin-top: 5px;
    max-width: 100vw;
    padding: 0em;
    /*padding: 2px 4px;*/
    display: flex;
    align-items: center;
    min-width: 10em;
    border-radius: 20px;
    background-color: rgba(139,107,97,0.3);
}



@media screen and (max-width: 350px) {
  button {

  }
}
@media screen  {
    hr {
        height: 0.3em;
    }
    .MuiChip-label {
        word-wrap: break-word;
        overflow: hidden;
        max-width: 90vw;
    }
    .heroIconGridEmpty {
        max-width: 5em;
        min-width: 5em;
        min-height: 6.1em;
        align-items: flex-end;
        display: flex;
        font-size: 0.6em;
        font-weight: 700;
        text-align: center;
        flex-basis: 0 !important;
        flex-grow: 0;
        margin: 0;
        border-radius: 2px;
    }

    .MuiToggleButtonGroup-root {
        display: flex !important;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        max-width: 100vw;
    }
    .MuiToggleButtonGroup-root > button {
        display: flex !important;
        padding: 0.5em;
        margin: 0 !important;
        border-left-color: rgba(0, 0, 0, 0.12) !important;
      /*  border-top-right-radius: 4px !important;
        border-bottom-right-radius: 4px !important;
        border-top-left-radius: 4px !important;
        border-bottom-left-radius: 4px !important;*/
       /* border-left-style: solid !important;*/
    }
    .App {
        text-align: center;
    }

    .App-logo {
        height: 40vmin;
        pointer-events: none;
    }

    @media (prefers-reduced-motion: no-preference) {
        .App-logo {
            animation: App-logo-spin infinite 20s linear;
        }
    }

    .App-header {
        background-color: #282c34;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: calc(10px + 2vmin);
        color: white;
    }

    .App-link {
        color: #61dafb;
    }

    .heroButtonThreeDots {
        font-size: 2em !important;
    }

    .heroQueueTwoVSBox {
        margin: 2px;
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 1fr 1fr;
    }

    .heroQueueTwoVSPosition {
        justify-content: center;
        align-items: center;
        border-radius: 200%;
        text-align: center;
        display: flex;
        background-color: rgba(255, 255, 255, 0.2);
        font-weight: bold;
    }

    .heroQueueTwoVSVS {
        justify-content: center;
        align-items: center;
        border-radius: 200%;
        text-align: center;
        display: flex;
        background-color: rgba(0, 0, 0, 0.2);
        font-weight: bold;
    }

    .heroPackSelected {
        justify-content: center;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1em;
        max-width: min-content;
        margin: 0 0 0 0;
        border-radius: 10px;

    }

    .heroPackStack > div {
        margin-top: 3px;
    }

    .heroPackSelected div {
        margin: 2px;
        word-break: break-word;
        font-size: 0.8em;
    }

    .heroHeader {
        display: flex;
        max-height: min-content;
    }

    .heroHeader > a {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1em;
        font-weight: 700;
        text-decoration: none;
    }

    .heroHeader > a > img {
        max-width: 9.96em;
        max-height: 5.6em;
        vertical-align: center;
    }

