        justify-content: center;
        align-items: center;
    }

    .heroEditPackButton {
        /* width: 100%*/
    }

    .heroGrid {

    }

    .closeButton {
        min-width: min-content !important;
        padding: 4px 4px 0 1px !important;
        align-items: flex-start !important;
    }

    .heroIcon {
        border-radius: 10px;
        overflow: hidden;
        border: 1px solid white;
        max-height: 5em;
        max-width: 5em;
        width: 5em;
        height: 5em;
        min-height: 5em;
        min-width: 5em;
        /*height: 80px;
        width: 80px;*/
    }

    .closeIcon {
        max-height: 1em;
        max-width: 1em;
        width: 1em;
        height: 1em;
        min-height: 1em;
        min-width: 1em;
        margin-left: 0.2em;
        /*height: 80px;
        width: 80px;*/
    }

    .heroIconSpan {
        width: 100%;
        text-align: center;
        word-break: break-word;
    }

    .heroPackSelectedExtStatus {
        /*grid-column: 1 / 6;
        grid-row: 2 / 2;*/
        /*justify-self: center;*/
        max-width: 89vw;
        overflow-wrap: break-word;
        /*font-size: 0.8em;*/
        padding: 4px;
        white-space: pre-wrap;
    }


    @keyframes App-logo-spin {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(360deg);
        }
    }

    .heroProb {
        /*position: absolute;*/
        padding: 0 0 1em 0;
        margin: -4em 1em 0 0.5em;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
        user-select: none;
        z-index: 10;
    }

    .heroIconGrid {
        flex-basis: 0 !important;
        max-width: max-content !important;
    }

    .heroCounterGrid {

    }

    @media screen and (min-width: 500px) {
        .heroQueueTwoBox {

        }
    }
